.head {
  width: 268px;
  height: 115px;

  margin-left: 10%;

  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 104px;
  line-height: 110.7%;

  /* or 115px */

  letter-spacing: -0.03em;
}

.line {
  width: 111px;
  height: 0px;
  text-decoration: underline;

  left: 197px;
  top: 345px;
  margin-left: 10%;

  text-decoration-color: #fc8e5b;
}

.scrollpage {
  width: 150px;
  height: 0px;
  background: #f5ea95;

  overflow: hidden;
  overflow-y: scroll;
}
.annonce {
  background-color: #f5ea95;
}

.title {
  font-family: Barlow;
  text-align: center;
  font-style: normal;
  font-weight: 600;
  color: #3d1734;

  padding-top: 2rem;
  padding-left: 1rem;
  padding-right: 1rem;
}
.txt {
  font-family: "Mulish", sans-serif;
  text-align: center;
  font-style: normal;
  font-weight: 600;
  color: #3d1734;
  line-height: 17.5px;
  padding: 1rem 1rem 4.5rem 1rem;
}
.scroll {
  padding: 0;
  width: 628px;
  height: 340px;
  overflow: hidden;
  overflow-y: scroll;
}
.dim {
  margin-top: 10%;
  margin-right: 30%;
}
@media (max-width: 600px) {
  .title {
    margin-bottom: 5rem;
  }
}
