.circle {
  position: relative;
  border-radius: 47%;
  width: 100%;
  height: 100%;
  background-color: #161616;
  padding: 20px;
  padding-top: 100%;

  z-index: 1;
}

.white-circle {
  position: absolute;
  width: 99%;
  height: 99%;
  border: 3px solid #fff;
  border-radius: 50%;
  box-sizing: border-box;

  left: 0%;
  z-index: 2;
  top: 1%;
}
.Hello-square {
  position: absolute;
  /* floutter le rectangle */
  -webkit-backdrop-filter: blur(4px);
